import { configureStore, getDefaultMiddleware } from '@reduxjs/toolkit';
import { createLogger } from 'redux-logger';
import BlogSlice from './slices/BlogSlice';
import WebSlice from './slices/WebSlice';
import CafeSlice from './slices/CafeSlice';
import BookSlice from './slices/BookSlice';
import ImageSlice from './slices/ImageSlice';
// Slice      const logger = createLogger();
const logger = createLogger();
const store = configureStore({
    //    Slice   . reducer: {
    reducer: {
        'web' : WebSlice,
        'blog' : BlogSlice,
        'image' : ImageSlice,
        'cafe' : CafeSlice,
        'book' : BookSlice,
    },    
   
    //       
    middleware: [...getDefaultMiddleware({serializableCheck: false}), logger], // redux-devtools-extension    false      devTools: true
    });
    export default store;